.font-title {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;

  @include onTablet {
    font-size: 72px;
  }

  @include onDesktop {
    font-size: 96px;
  }

  &--menu-h5-h3 {
    font-size: 28px;
    line-height: 125%;

    @include onTablet {
      font-size: 40px;
      line-height: 125%;
    }
    @include onDesktop {
      font-size: 48px;
      line-height: 120%;
    }
  }

  &--h2 {
    font-size: 38px;

    @include onTablet {
      font-size: 62px;
    }

    @include onDesktop {
      font-size: 80px;
    }
  }

  &--h3 {
    font-size: 28px;
    line-height: 125%;

    @include onTablet {
      font-size: 40px;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 120%;
    }
  }

  &--h4 {
    font-size: 20px;
    line-height: 125%;

    @include onTablet {
      font-size: 24px;
    }
  }

  &--h5 {
    font-size: 20px;
    line-height: 150%;
  }

  &--h6 {
    font-size: 14px;
    line-height: 125%;
  }
}
